.addProduct {
  .add_products_title {
    font-family: Helvetica;
    font-size: 32px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: 0em;
    text-align: left;
  }
  .title_product {
    font-family: Helvetica;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
  }
  .Product_description {
    font-family: "Helvetica";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #000000;
    margin-top: 10px;
  }
}
.boder_box {
  background-color: #fff;
  padding: 20px;
  border-radius: 12px;
}
.end_date {
  display: flex;
  align-items: end;
}
